import { Injectable } from '@angular/core';

import { Meal } from '../models/meal.model';

@Injectable({providedIn: 'root'})
export class MealService {
  meals: Meal[] = [
    new Meal(
      'Breakfast',
      []
    ),
    new Meal(
      'Lunch',
      []
    ),
    new Meal(
      'Dinner',
      []
    ),
    new Meal(
      'Dessert',
      []
    )
  ];

  mealSelected: Meal = this.meals[0];

  constructor() { }

  getMeals() {
    return this.meals.slice();
  }

  resetMeals() {
    this.meals =[
      new Meal(
        'Breakfast',
        []
      ),
      new Meal(
        'Lunch',
        []
      ),
      new Meal(
        'Dinner',
        []
      ),
      new Meal(
        'Dessert',
        []
      )
    ];
  }
}


